@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
body {
  margin: 0;
  background-color: white;

  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  padding-left: 1%;
  padding-right: 1%;
  padding-top: 1%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.contacts_header {
  background-color: white;
  color: blue;
  width: 100%;
  padding-left: 28px;
}

.listitem {
  width: 100%;
  height: 50px;
}
.content .subheader {
  padding-left: 30px;
  color: blue;
  font-weight: bolder;
  font-size: 15px;
}

.root {
  background-color: white;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  height: 60px;
  margin-left: 18px;
}

.header .new_button {
  border-radius: 30px;
  text-transform: none;
  font-size: 85%;
  min-height: 8px;
  min-width: 110px;
}

.header .search_icon {
  display: flex;
  justify-content: right;
  margin-right: 18px;
}

.contacts_icon {
  margin-left: 18px;
  margin-bottom: 15px;
}

.contact_main {
  width: 100%;
}
.contact_main .contact_avatar {
  display: flex;
  justify-content: center;
  margin-top: 5%;
  margin-bottom: 10%;
}

.contact_main .contact_header {
  display: flex;
  justify-content: space-between;
  height: 60px;
  margin-left: 4px;
  margin-right: 4px;
  font-size: 85%;
}

.content2 {
  width: 100%;
  display: flex;
  justify-content: left;
  font-size: 35px;
  margin-left: 15px;
  margin-top: 5px;
  margin-bottom: 5px;
}

.icons_group {
  display: flex;
  justify-content: space-between;
  margin-left: 15px;
  margin-right: 15px;
}

.content3 {
  width: 100%;
  color: blue;
  display: flex;
  justify-content: left;
  font-size: 20px;
  margin-left: 15px;
  margin-right: 15px;
}

.content5 {
  margin-left: 15px;
  margin-right: 15px;
}

.add_class {
  margin-left: 18px;
  margin-right: 18px;
}

.add_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  height: 60px;
}

.add_header .cancel_button {
  border-radius: 20px;
  text-transform: none;
  font-size: 85%;
  min-height: 8px;
  min-width: 80px;
}

.add_header .done_button {
  border-radius: 20px;
  text-transform: none;
  font-size: 85%;
  min-height: 8px;
  min-width: 80px;
}

.newcontact {
  margin-left: 3px;
}

.object .object1 {
  display: flex;
  justify-content: center;
}

.input_sizing {
  width: 100%;
  margin-top: 15px;
}

.input_width {
  width: 100%;
}

.header {
  display: flex;
  justify-content: space-between;
}

.header .header_button {
  border-radius: 30px;
  text-transform: none;
  font-size: 85%;
  min-height: 8px;
  min-width: 110px;
}

.search_label {
  margin-left: 18px;
}

.search {
  padding-left: 15px;
  padding-right: 15px;
  margin-top: 10px;
}

.search .search_input {
  background-color: white;
  color: blue;
  font-size: 18px;
  height: 40px;
  width: 100%;
  border: 2px solid white;
  border-radius: 2px;
  padding: 2px 23px 2px 23px;
  outline: 0;
  box-shadow: rgba(18, 28, 179, 0.35) 0px 5px 15px;
}

.search_input:hover,
.search_input:focus {
  border: 2px solid white;
  background-color: white;
}

.dataResult {
  margin-top: 0px;
  width: 100%;
  height: auto;
  background-color: white;
  box-shadow: rgba(18, 28, 179, 0.35) 0px 5px 15px;
  overflow: hidden;
  overflow-y: auto;
}

.dataResult::-webkit-scrollbar {
  display: none;
}

.dataResult .dataItem {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  color: black;
}

.dataItem p {
  margin-left: 10px;
}
a {
  text-decoration: none;
}

a:hover {
  background-color: lightgrey;
}

#clearBtn {
  cursor: pointer;
}

